export async function getHashtags(inputHashtags) {
  return new Promise(resolve => {
    let hashtags;
    if (inputHashtags[0] != null) {
      hashtags = inputHashtags;
    } else {
      hashtags = "undefined";
    }
    let endArr = [];
    endArr.push(hashtags);
    resolve(endArr);
  });
}
